

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

html {
  width: 100%;
}

body {
  color: rgb(75 85 99);
  width: 100%;
}

.cover {
  background: url("./images/cover-main.webp") no-repeat center center/cover;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cover .cover-title {
  background: url("./images/circle.webp") no-repeat center center/contain;
  width: 500px;
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.cover h3 {
  font-size: 2rem;
}

.cover-text {
  margin: 40px 0;
  font-size: 2rem;
  display: flex;
}

.cover img {
  width: 100px;
}

.time-item {
  margin: 0 30px;
  text-align: center;
}

.time-item p:first-child {
  margin-bottom: 20px;
}

.kjsadbf {
  background-color: rgb(122 142 133);
  height: 24rem;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  flex-direction: column;
  text-align: center;
}

.kjsadbf h4 {
  font-size: 2rem;
  margin-bottom: 30px;
}

.kjsadbf p {
  font-size: 1.3rem;
}

.info {
  background: url("./images/cover-main.webp") no-repeat center center/cover;
}

.list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-wrap: wrap;
  gap: 10rem;
  padding: 6rem 2.5rem;
}

.list img {
  width: 8rem;
}

.info-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.info-item .top {
  background: url("./images/circle-item.webp") no-repeat center center/cover;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 250px;
  height: 250px;
}

.info-item h4 {
  margin: 1rem 0;
}

.info-item .descr {
  margin: 1rem 0;
}

.info-item a {
  cursor: pointer;
  color: rgb(75 85 99);
}

@media screen and (max-width: 550px) {
  .cover .cover-title {
    width: 400px;
    height: 400px;
  }

  .cover h3 {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 401px) {
  .cover .cover-title {
    width: 350px;
    height: 350px;
  }

  .cover h3 {
    font-size: 1.6rem;
  }
}